 :root {
	--ck-sample-editor-min-height: 300px;
}

.presence {
	display: flex;
	justify-content: flex-start;
	padding: 0;
}

.live-snippet .row {
	display: flex;
	position: relative;
	margin: 20px 0;
}

.sidebar {
	min-width: 300px;
	font-size: 20px;
	padding: 0 10px;
	border: 1px solid var(--ck-color-toolbar-border);
	margin-left: -1px;
	background: var(--ck-color-toolbar-background);

	/* Do not scroll the sidebar with comments or suggestions. */
	overflow: hidden;
}

.sidebar.narrow {
	min-width: 65px;
}

.sidebar.hidden {
	display: none;
}

/* Disable the default editor margin so that the editor is aligned horizontally with the comments sidebar. "*/
.live-snippet .ck.ck-editor {
	margin: 0;
	width: 100%;
}

.live-snippet .ck.ck-editor .ck-content {
	min-height: var(--ck-sample-editor-min-height);
}

.live-snippet .ck-comments-archive .ck-editor .ck-content {
	min-height: auto;
}

 /* Shortened the heading dropdown to fit more buttons. */
.live-snippet .ck-heading-dropdown {
	width: 108px;
}

/* Override docs styles. */
.sidebar .ck-sidebar ul.ck-thread__comments {
	padding: 0;
	margin: 0;
	list-style-type: none;

	/* Remove override (.formatted ul). */
	line-height: 1em;
}

.sidebar .ck-sidebar .ck.ck-content {
	padding: 0;
}

/* Change the design of the presence list (aligned to the left instead of the default right). */
.presence .ck.ck-presence-list__counter {
	margin-left: var(--ck-spacing-standard);

	/* Move `info 1 connected user (me)` next to the avatar. */
	order: 2;
}

/* Hide the editor content before it is loaded properly. */
.live-snippet {
	min-height: 200px;
}

.live-snippet__container {
	/* #91 */
	overflow: auto;

	opacity: 0;
	display: none;
}

.live-snippet__container.loaded {
	display: block;
	opacity: 1;
	animation: 300ms _fadein ease-in-out;
}

.live-snippet__loader {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 1;
	width: 100%;
	text-align: center;
	margin: 50px auto;
}

.live-snippet__loader.fadeout {
	display: none;
	animation: 300ms _fadeout ease-in-out;
}

.live-snippet__loader .spinner {
	display: block;
	height: 75px;
	width: 75px;
}

.live-snippet__loader .spinner .path {
	stroke-dasharray: 1, 200;
	stroke-dashoffset: 0;
	animation: _spinner-color 6s ease-in-out infinite, _spinner-dash 1.5s ease-in-out infinite;
	stroke-linecap: round;
}


@keyframes _fadein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes _fadeout {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

@keyframes _spinner-color {
	100%,
	0% {
		stroke: #f75b4b;
	}

	40% {
		stroke: #0387d0;
	}

	66% {
		stroke: #1ebd60;
	}

	80%,
	90% {
		stroke: #8d44ad;
	}
}

@keyframes _spinner-dash {
	0% {
		stroke-dasharray: 1, 200;
		stroke-dashoffset: 0;
	}

	50% {
		stroke-dasharray: 89, 200;
		stroke-dashoffset: -35px;
	}

	100% {
		stroke-dasharray: 89, 200;
		stroke-dashoffset: -124px;
	}
}
